import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

import axios from 'axios'

const state = {
    indexLoading: false,
    indexWeeklyList:[],
    indexMonthlyList:[],
    indexAnnualList:[],
    alertText:'',
    rankingLoading:false,
    rankingHeader: {},
    rankingData:[]
}

const actions = {
    async search_index(context){
        context.commit('CHANGE_INDEX_LOADING')
        //搜索周度排名索引
        await axios.get("https://lemonade542.github.io/vocal-ranking/ranking/weekly/index.json").then(
            response => {
                context.commit('GET_WEEKLY_INDEX', response.data.index.reverse())
            },
            error => {
                context.commit('SHOW_ALERT', error.message)
            }
        )
        //搜索月度排名索引
        await axios.get("https://lemonade542.github.io/vocal-ranking/ranking/monthly/index.json").then(
            response => {
                context.commit('GET_MONTHLY_INDEX', response.data.index.reverse())
            },
            error => {
                context.commit('SHOW_ALERT', error.message)
            }
        )
        //搜索年度排名索引
        await axios.get("https://lemonade542.github.io/vocal-ranking/ranking/annual/index.json").then(
            response => {
                context.commit('GET_ANNUAL_INDEX', response.data.index.reverse())
            },
            error => {
                context.commit('SHOW_ALERT', error.message)
            }
        )
        await context.commit('CHANGE_INDEX_LOADING')
    },
    get_ranking(context, value){
        context.commit('CLEAR_RANKING_DATA')
        context.commit('CHANGE_RANKING_LOADING')
        const category = value.split('-')[0]
        const url = `https://lemonade542.github.io/vocal-ranking/ranking/${category}/${value}.json`
        axios.get(url).then(
            response => {
                context.commit('GET_RANKING_DATA',response.data)
                context.commit('CHANGE_RANKING_LOADING')
            },
            error => {
                context.commit('SHOW_ALERT', error.message)
                context.commit('CHANGE_RANKING_LOADING')
            }
        )
    }
}

const mutations = {
    CHANGE_INDEX_LOADING(state){
        state.indexLoading = !state.indexLoading
    },
    SHOW_ALERT(state, result){
        state.alertText = result
    },
    GET_WEEKLY_INDEX(state, result){
        state.indexWeeklyList = result
    },
    GET_MONTHLY_INDEX(state, result){
        state.indexMonthlyList = result
    },
    GET_ANNUAL_INDEX(state, result){
        state.indexAnnualList = result
    },
    CHANGE_RANKING_LOADING(state){
        state.rankingLoading = !state.rankingLoading
    },
    CLEAR_RANKING_DATA(state){
        state.rankingHeader = {}
        state.rankingData = []
    },
    GET_RANKING_DATA(state, result){
        state.rankingHeader = result.header
        state.rankingData = result.data
    }
}

const store = new Vuex.Store({
    state,
    actions,
    mutations,
})

export default store