<script>
import {mapState} from "vuex"
export default {
  name: 'Search',
  data(){
    return {
      category: '',
      showAlert: false,
    }
  },
  computed:{
    ...mapState(['indexLoading','indexMonthlyList','indexWeeklyList','indexAnnualList','alertText'])
  },
  watch:{
    alertText(){
      if(this.alertText!==''){
        this.showAlert = true
      }
    }
  },
  methods:{
    searchRanking(){
      let selected = ''
      if(this.category === 'weekly'){
        selected = this.$refs.selectWeekly.value
      }
      if(this.category === 'monthly'){
        selected = this.$refs.selectMonthly.value
      }
      if(this.category === 'annual'){
        selected = this.$refs.selectAnnual.value
      }
      if(selected===''){
        alert("请先选择类别")
      } else {
        this.$store.dispatch('get_ranking',selected)
        this.$router.replace({name:'ranking',params:{ranking_file_name:selected}})
      }
    },
    goHome(){
      this.$router.replace({name:'home'})
    }
  },
  mounted(){
    this.$store.dispatch('search_index')
  }
}
</script>

<template>
  <div>
    <div class="alert alert-danger alert-dismissible" v-show="showAlert">
      <button type="button" class="btn-close" data-bs-dismiss="alert" @click="!showAlert"></button>
      {{alertText}}
    </div>
    <div class="spinner-border" v-show="indexLoading"></div>
    <div class="row" v-show="!indexLoading">
      <div class="col-md-4">
        <select class="form-select" v-model="category">
          <option value="">请选择类别</option>
          <option value="weekly">每周</option>
          <option value="monthly">每月</option>
          <option value="annual">每年</option>
        </select>
      </div>
      <!-- 空索引 -->
      <div class="col-md-8" v-show="category===''">
        <select class="form-select">
          <option>请先选择类别</option>
        </select>
      </div>
      <!-- 周索引 -->
      <div class="col-md-8" v-show="category==='weekly'">
        <select class="form-select" ref="selectWeekly">
          <option v-for="(rank,index) in indexWeeklyList" :key="index" :value="rank.ranking_file_name">
            {{rank.ranking_name}}({{rank.ranking_start_date}}-{{rank.ranking_end_date}})
          </option>
        </select>
      </div>
      <!-- 月索引 -->
      <div class="col-md-8" v-show="category==='monthly'">
        <select class="form-select" ref="selectMonthly">
          <option v-for="(rank,index) in indexMonthlyList" :key="index" :value="rank.ranking_file_name">
            {{rank.ranking_name}}({{rank.ranking_start_date}}-{{rank.ranking_end_date}})
          </option>
        </select>
      </div>
      <!-- 年索引 -->
      <div class="col-md-8" v-show="category==='annual'">
        <select class="form-select" ref="selectAnnual">
          <option v-for="(rank,index) in indexAnnualList" :key="index" :value="rank.ranking_file_name" v-show="indexAnnualList.length!==0">
            {{rank.ranking_name}}({{rank.ranking_start_date}}-{{rank.ranking_end_date}})
          </option>
          <option v-show="indexAnnualList.length===0" value="">暂无数据</option>
        </select>
      </div>
    </div>
    <div class="row" style="margin-top: 1em">
      <div class="col">
        <button type="button" class="btn btn-primary" @click="searchRanking">检索</button>
        <button type="button" class="btn btn-primary" @click="goHome" style="margin-left: 1em">回到首页</button>
      </div>
    </div>
  </div>
</template>

<style scoped>

</style>