<script>
import {mapState} from "vuex"
export default {
  name: "Ranking",
  computed:{
    ...mapState(['rankingLoading','rankingHeader','rankingData'])
  },
}
</script>

<template>
  <div>
    <div class="spinner-border" v-show="rankingLoading"></div>
    <div v-show="!rankingLoading">
      <h3>{{rankingHeader.ranking_name}}热门曲排行</h3>
      <div style="margin-top:0.5em;margin-bottom:0.5em">数据统计时间：{{rankingHeader.ranking_start_date}} 07:00 - {{rankingHeader.ranking_end_date}} 07:00 (GMT+9)</div><br/>
      <a class="btn btn-primary" :href="`https://www.bilibili.com/video/${rankingHeader.ranking_video_bili_id}`" target="_blank">观看本期视频</a>
      <table class="table">
        <thead>
        <tr>
          <th class="col-9">歌曲</th>
          <th class="col-3">数据</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="data in rankingData" :key="data.ranking">
          <td>
            <strong style="font-size:20px">#{{data.ranking}}  </strong>
            <span v-if="data.last_score===0">▲</span>
            <span v-else-if="data.last_score!==-1&&data.ranking-data.last_ranking<0">▲</span>
            <span v-else-if="data.last_score!==-1&&data.ranking-data.last_ranking>0">▲</span>
            <span v-else></span>
            <span v-if="data.last_score===-1">NEW!!</span>
            <span v-else-if="data.last_score===0">--</span>
            <span v-else>{{Math.abs(data.ranking-data.last_ranking)}}</span><br/>
            <span><strong style="font-size: 20px">{{data.song_title}}</strong></span><br/>
            {{data.song_artist}} / {{data.song_vocalbank}}<br/>
            {{data.start_time}}<br/>
            {{data.content_id}}<br/>
          </td>
          <td>
            <span>播放：{{data.weight_x.toFixed(2)}}</span>×<span><strong>{{data.view_delta.toLocaleString()}}</strong></span><br/>
            <span>评论：{{data.weight_a.toFixed(2)}}</span>×<span><strong>{{data.comment_delta.toLocaleString()}}</strong></span><br/>
            <span>收藏：{{data.weight_b.toFixed(2)}}</span>×<span><strong>{{data.mylist_delta.toLocaleString()}}</strong></span><br/>
            <span>点赞：{{data.weight_b.toFixed(2)}}</span>×<span><strong>{{data.like_delta.toLocaleString()}}</strong></span><br/>
            <span>总分：<strong>{{data.total_score.toLocaleString()}}</strong>PTS </span>
            <span v-if="data.last_score===0">▲</span>
            <span v-else-if="data.last_score!==-1&&data.total_score-data.last_score>0">▲</span>
            <span v-else-if="data.last_score!==-1&&data.total_score-data.last_score<0">▼</span>
            <span v-else></span>
            <span v-if="data.last_score===-1">NEW!!</span>
            <span v-else-if="data.last_score===0">--%</span>
            <span v-else>{{Math.abs((data.total_score/data.last_score-1)*100).toFixed(2)+"%"}}</span>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<style scoped>

</style>